export default {
	name: 'HomeHeader',
	data() {
		return {
			now: new Date()
		}
	},
	props: {
		pageTitle: String
	},
	computed: {
		todayDay() {
			return this.now.getDate()
		},
		todayDate() {
			return this.now.getFullYear()+ '.' + Number(this.now.getMonth()+1)
		}
	},
	methods: {
		categoryClick () {
			wx.showToast({
				title: '切换类别',
				icon: 'none'
			})
		}
	}
}
